import React from 'react';
import { Typography, makeStyles } from '@material-ui/core';
import classNames from 'classnames';
import FlexBox from './FlexBox';
import { useHistory } from 'react-router-dom';
import { PageRoutes } from '../models/routes';

const useStyles = makeStyles((theme) => ({
  headerLabelContainer: {
    cursor: 'pointer',
    flexDirection: 'column',
  },
  headerLabel: {
    fontFamily: 'AngelicBonques',
  },
  studioLabel: {
    fontFamily: 'AngelicBonquesScript',
    fontSize: 24,
  },
  labelSvg: {
    height: 72,
  },
}));

type HeaderLabelProps = { className?: string };
const HeaderLabel = ({ className }: HeaderLabelProps) => {
  const classes = useStyles();
  const history = useHistory();
  return (
    <FlexBox className={classNames(classes.headerLabelContainer, className)} onClick={() => history.push(PageRoutes.Root)}>
      <Typography className={classes.headerLabel} variant='h3'>
        TY KARGES HOME
      </Typography>
      <Typography color='primary'>
        HOME STAGING + INTERIOR DESIGN
      </Typography>
    </FlexBox>
  );
};

export default HeaderLabel;