import React, { useState } from 'react';
import { Tabs, makeStyles } from '@material-ui/core';
import HeaderTabLink, { HeaderTabWithDropdown } from './HeaderTabLink';
import { PageRoutes } from '../models/routes';

const useStyles = makeStyles((theme) => ({
  tabsContainer: {
    position: 'relative',
  },
  contactTab: {
    bottom: 0,
    right: 0,
    width: 158,
    height: 200,
    display: 'flex',
    position: 'absolute',
    background: '#b2cae4',
    zIndex: -1,
  },
}));

type HeaderTabsProps = { orientation?: 'horizontal' | 'vertical' }
const HeaderTabs = ({ orientation }: HeaderTabsProps) => {
  const classes = useStyles();
  const [tabValue, setTabValue] = useState(0);
  return (
    <div className={classes.tabsContainer}>
      <Tabs orientation={orientation} value={tabValue} onChange={(evt, newValue) => {
        setTabValue(newValue);
      }} indicatorColor='primary' centered>
        <HeaderTabLink href={PageRoutes.Services} label='Services' />
        <HeaderTabWithDropdown href={PageRoutes.Portfolio} label='Portfolio' subItems={[{ href: '/services/interior', label: 'Staging + Interiors' }, { href: '/services/modeling', label: '3D Modeling' }]} />
        <HeaderTabLink href={PageRoutes.About} label='About' />
        <HeaderTabLink href={PageRoutes.Contact} label='Contact' />
      </Tabs>
      <div className={classes.contactTab} />
    </div>
  );
};

export default HeaderTabs;