import React, { useState } from 'react';
import { useMediaQuery, useTheme, Backdrop, IconButton } from '@material-ui/core'
import MenuRoundedIcon from '@material-ui/icons/MenuRounded';
import HeaderTabs from './HeaderTabs';

const HeaderMenu = () => {
  const theme = useTheme();
  const isLargeWidth = useMediaQuery(theme.breakpoints.up('md'));
  const [open, setOpen] = useState(false);
  if (isLargeWidth) {
    return <HeaderTabs />;
  } else {
    return (
      <>
        <IconButton onClick={() => setOpen((prevOpen) => !prevOpen)}>
          <MenuRoundedIcon />
        </IconButton>
        <Backdrop open={open} onClick={() => setOpen(false)}>
          <HeaderTabs orientation='vertical' />
        </Backdrop>
      </>
    );
  }
};

export default HeaderMenu;